@import url(https://fonts.googleapis.com/css?family=Open+Sans);
html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  background-image: url(/berry-display/static/media/symphony.fcb1692c.png);
  background-attachment: fixed;
  font-family: "Open Sans", sans-serif;
  color: #422833; }

.app {
  display: -webkit-flex;
  display: flex;
  position: relative;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  min-height: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  background: -webkit-linear-gradient(rgba(161, 163, 129, 0.25), rgba(161, 163, 129, 0.25));
  background: linear-gradient(rgba(161, 163, 129, 0.25), rgba(161, 163, 129, 0.25)); }

nav {
  font-size: 10px;
  position: fixed;
  bottom: 0;
  width: 100%; }
  nav a {
    text-decoration: none;
    color: #678927; }
    nav a:visited {
      color: #678927; }
    nav a:hover {
      color: white; }
    nav a:active {
      color: #678927; }

.info-panel {
  margin: 30px;
  margin-bottom: 50px;
  width: 300px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 1px 6px 13px 0px #414b4f; }

header {
  padding: 10px;
  background-color: #A9D15F; }
  header p {
    display: inline-block;
    margin-right: 10px; }
  header img {
    vertical-align: middle; }
  header h2 {
    display: inline-block; }

.details {
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  background-color: #eeeeea; }
  .details meter {
    width: 100%;
    border-radius: 5px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: white; }
  .details meter::-webkit-meter-optimum-value, .details meter::-moz-meter-bar {
    background: none;
    background-color: #A9D15F;
    border-radius: 5px; }
  .details .size {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    text-align: center; }
  .details .base-stats {
    -webkit-flex-grow: 1;
            flex-grow: 1; }
    .details .base-stats ul {
      list-style: none;
      padding: 0;
      font-size: 20px; }
      .details .base-stats ul .flavor-text {
        font-size: 10px;
        margin-bottom: 5px; }

